/**
 *
 * Tabbed content in panes.
 *
 * This is table-styled stuff that fits in the sidebar panes. It's used for things like player
 * splits in the draft section, prize structures in the lobby, etc..
 *
*/
.pane .tab-nav {
  text-align: left;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  color: #b0b2c1;
  padding-bottom: 30px; }
  .pane .tab-nav ul {
    padding: 0;
    margin: 0; }
  .pane .tab-nav li {
    margin: 5px 40px 5px 0;
    display: inline-block;
    cursor: pointer;
    line-height: 2em; }
    .pane .tab-nav li:last-child {
      margin: 0;
      padding: 5px 0 0 0; }
      .pane .tab-nav li:last-child a {
        color: #b0b2c1;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 10px; }
        .pane .tab-nav li:last-child a:hover {
          color: #262a3b;
          cursor: pointer;
          text-decoration: none; }
      .pane .tab-nav li:last-child .rotowire-icon {
        background-image: url("../img/common/rwlogo-500w.png");
        background-position: 1px 4px;
        background-repeat: no-repeat;
        margin-top: -7px;
        height: 26px;
        width: 70px;
        display: inline-block;
        background-size: contain;
        vertical-align: top; }
    .pane .tab-nav li:hover {
      color: #262a3b; }
    .pane .tab-nav li.active {
      color: #262a3b;
      position: relative; }
      .pane .tab-nav li.active .tab-title:before {
        content: '';
        width: 23px;
        border-bottom: 2px solid #46495e;
        position: absolute;
        left: 0;
        height: 1px;
        bottom: -5px; }

